.default-select {
   &.style-1 {
      width: auto !important;
      height: auto;
      .btn {
         font-size: 12px;
         padding: 5px;
         border: 0 !important;
         &:after {
            margin-left: 0.5em;
            font-size: 16px;
         }
      }
      .dropdown-menu {
         z-index: 1;
         .dropdown-item {
            font-size: 12px;
         }
      }
   }
   &.style-2 {
      .btn {
         padding: 15px 30px;
         &:after {
            color: $primary;
            margin-left: 8px;
            font-size: 20px;
         }
      }
      @include respond("laptop") {
         .btn {
            padding: 0.625rem 1rem;
