}

.card-tabs {
   .nav-tabs {
      border-bottom: 0px;
      background: transparent;
      padding: 5px;
      border-radius: 0;
      flex-wrap: unset;

      .nav-link {
         border-radius: 0;
         background: transparent;
         padding: 10px 14px;
         font-weight: 500;
         border-bottom: 1px solid $border-color !important;
         font-size: 14px;
         border: 0;
         color: $black;
         position: relative;
         &:after {
            position: absolute;
            bottom: 0;
            left: 0;
            height: 0px;
            width: 100%;
            background: $primary;
            content: "";
         }
         &.active {
            background: transparent;
            color: $primary;
            &:after {
               height: 2px;
            }
         }
         @include respond("laptop") {
            padding: 8px 15px;
            font-weight: 400;
            font-size: 14px;
         }
      }
      &.tabs-lg {
         .nav-link {
            padding: 23px 30px;
            font-size: 16px;
            @include respond("phone-land") {
               padding: 10px 15px;
               font-size: 14px;
            }
         }
      }
      @include respond("phone-land") {
         flex-wrap: wrap;
      }
   }
   &.icontabs {
      .nav-tabs {
         background: transparent;
         .nav-item {
            .nav-link {
               height: 42px;
               width: 42px;
               line-height: 42px;
               text-align: center;
               background: rgba($gray, 0.6);
               color: $white;
               margin-right: 15px;
               border-radius: 50px;
               padding: 0;
               &.active {
                  background: $warning;
               }
               i {
                  font-size: 25px;
                  line-height: 42px;
               }
            }
         }
      }
   }
   &.style-1 {
      .nav-tabs {
         background: transparent;
         .nav-link {
            font-size: 16px;
            color: $black;
            position: relative;
            &:after {
               content: none;
            }
            span {
               position: absolute;
               bottom: -4px;
               left: 50%;
               transform: translatex(-50%);
               content: "";
               height: 4px;
               width: 0;
               @include transitionMedium;
            }
            &.active {
               span {
                  width: 100%;
               }
            }
         }
      }
   }
}

